.sidebarWrapper {
    position: relative;
    height: 800px;
    /* border: 2px solid red; */
    margin-left: 50px;
}

.sidebarDiv {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: column;
    /* border: 2px solid olivedrab; */
    /* background: linear-gradient(blue, black); */
    width: fit-content;
    height: 100%;
    /* flex-basis: 100%; */
    /* background: linear-gradient(to bottom, gray, rgb(39, 39, 39)); */
    background: linear-gradient(to bottom, gray, rgb(59, 59, 59));
    /* padding-left: 20px; */
    padding-right: 20px;
    height: 500px;
    position: relative;
    transition: .7s ease-in-out;
    opacity: 0;
    transform: translateY(-50px);
}

.sidebarDivPopIn {
    opacity: 1;
    transform: translateY(0px);
}

.sidebarReflection {
    /* position: absolute; */
    margin-top: 3px;
    background: linear-gradient(rgba(42, 42, 42, 0.6), rgba(211, 211, 211, 0.0));
    width: 100%;
    height: 150px;
    transition: .7s ease-in-out;
    opacity: 0;
    transform: translateY(50px);
}

.sidebarReflectionPopIn {
    opacity: 1;
    transform: translateY(0px);
}

.logoDiv {
    display: flex;
    align-items: center;
    justify-content: center;
    /* border: 1px solid black; */
    width: 100%;
    position: relative;
}

.logoDiv::after{
    position: absolute;
    content: "";
    background-color: rgb(62, 62, 62);
    width: 90%;
    height: 1px;
    bottom: 10px;
    left: 20px;
}

.logoDiv .gregLogo {
    font-family: Italiana;
    width: fit-content;
    /* text-transform: ; */
    color: rgb(34, 34, 34);
}

.logoDiv .lewisLogo {
    font-family: Italiana;
    width: fit-content;
    color: rgb(206, 206, 206);
}

.sidebarList {
    list-style-type: none;
    padding: 0px;
    margin: 0;
    padding-top: 10px;
}

/* ABeeZee|Allura|Italiana|Montserrat|Philosopher
|Playball|Poiret+One|Quicksand|Sofia */
.sidebarList li {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    font-family: Quicksand;
    color: rgb(211, 211, 211);
    height: 30px;
    font-size: 20px;
    padding-left: 30px;
    padding-top: 5px;
    padding-bottom: 5px;
    /* border: 1px solid white; */
    margin-bottom: 5px;
    width: 200px;
    position: relative;
    perspective: 1000px;
    /* box-shadow: 0 10px 10px 0 rgb(0, 0, 0); */
    position: relative;
    cursor: pointer;
}

.sidebarList li p:nth-child(1) {
    transition: 0.4s;
}

.sidebarList li p:nth-child(2) {
    position: absolute;
    top: 0;
    margin-top: 10px;
    opacity: 0;
    transform: rotateY(-87deg);
    transition: 0.4s;
    color: rgb(191, 240, 235);
    text-shadow: 0 0 2px rgb(128, 236, 226);
}

.sidebarList li .glowUpItem {
    color: rgb(191, 240, 235);
    text-shadow: 0 0 2px rgb(128, 236, 226);
}

.sidebarList li .matureItemTransformOrigin {
    transform-origin: left;
}
.sidebarList li .glowUpMatureItem {
    color: rgb(241, 155, 155) !important;
    text-shadow: 0 0 2px rgb(236, 8, 0) !important;
}


.sidebarList li .hideItem {
    transform: rotateY(-87deg);    
    opacity: 0 !important;
}

.sidebarList li .showAlternative {
    opacity: 1 !important;
    transform: rotateY(0deg) !important;
}

.sidebarList li:not(:first-child) {
    margin-top: 3px;
}

