.container {
  position: relative;
  width: 100%;
  height: 100%;
  min-height: 600px;
  /* border: 2px solid tomato; */
}
 
.app span:nth-child(1) {
  position: absolute;
  background-color: rgb(188, 231, 225);
  width: 100%;
  height: 1.5px;
  box-shadow: 0 0 10px .5px rgba(255, 255, 255, 0.6);
  top: 0;
  left: 10%;
  -webkit-transform: translate(-10%, -50%);
          transform: translate(-10%, -50%);
  -webkit-transition: 1s cubic-bezier(0.785, 0.135, 0.15, 0.86);
  transition: 1s cubic-bezier(0.785, 0.135, 0.15, 0.86);
  /* max-width: 100%; */
  max-width: 0%;
  /* animation: 1s rotate infinite both; */
}

.app span:nth-child(2) {
  position: absolute;
  background-color: rgb(188, 231, 225);
  width: 100%;
  height: 1.5px;
  box-shadow: 0 0 10px .5px rgba(255, 255, 255, 0.5);
  bottom: 0;
  right: -10%;
  -webkit-transform: translate(-10%, -50%);
          transform: translate(-10%, -50%);
  -webkit-transition: 1s cubic-bezier(0.785, 0.135, 0.15, 0.86);
  transition: 1s cubic-bezier(0.785, 0.135, 0.15, 0.86);
  /* max-width: 100%; */
  max-width: 0%;
  /* animation: 1s rotate infinite both; */
}


.app span:nth-child(3) {
  position: absolute;
  background-color: rgb(188, 231, 225);
