}

.enterBoxNeon {
  max-height: 100% !important;
  max-width: 100% !important;
}

.leaveBoxNeon1 {
  left: 200% !important;
}

.leaveBoxNeon2 {
  right: 200% !important;
}

.leaveBoxNeon3 {
  bottom: 100% !important;
}

.leaveBoxNeon4 {
  top: 200% !important;
}

@-webkit-keyframes rotate {
  0% {
    max-width: 0%;
  }
  50% {
    max-width: 100%;
  }
}

@keyframes rotate {
  0% {
    max-width: 0%;
  }
  50% {
    max-width: 100%;
  }
}


.navbarAndMainDiv {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: column;
