}

.sidebarDiv {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: column;
    /* border: 2px solid olivedrab; */
    /* background: linear-gradient(blue, black); */
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: 100%;
    /* flex-basis: 100%; */
