.imageNavItem {
    /* border: 1.5px solid black; */
    cursor: pointer;
    background-color: rgb(7, 23, 23);
    border: 1.5px solid transparent;
    width: 100px;
    height: 135px;
    box-shadow: 0 0 3px 0 black;
    -webkit-transition: 0.5s;
    transition: 0.5s;
    opacity: 0;
    -webkit-transform: translateY(-20px);
            transform: translateY(-20px);
}

.imageNavItemWrapper:hover .imageNavItem {
    border: 1.5px solid turquoise;
}

.imageNavItemWrapper:hover .imageNavItemReflection {
    -webkit-transition: 0.3s;
    transition: 0.3s;
    box-shadow: inset 0 2px 1px 0 rgba(64, 224, 208, 0.51);
}

.imageNavItemShow {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
}

.imageNavItemWrapper:not(:first-child) {
    margin-left: 50px;
}

.imageNavItemReflection {
    margin-top: 3px;
    height: 50px;
    width: 100%;
    background: -webkit-gradient(linear, left top, left bottom, from(rgba(47, 79, 79, 0.5)), to(rgba(0, 0, 0, 0)));
    background: linear-gradient(rgba(47, 79, 79, 0.5), rgba(0, 0, 0, 0));
    -webkit-transition: 0.5s ease-in-out;
    transition: 0.5s ease-in-out;
    opacity: 0;
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
}

.imageNavItemReflectionShow {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
}

