@import url('https://fonts.googleapis.com/css?family=ABeeZee|Allura|Italiana|Montserrat|Philosopher|Playball|Poiret+One|Quicksand|Sofia&display=swap');

.app {
  /* background-color: black; */
  min-height: 100vh;
  /* background: linear-gradient(black, rgb(5, 1, 20));  */
  background-color: rgb(191, 191, 191);
  overflow: hidden;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  /* flex-direction: column; */
}

.container {
  position: relative;
  width: 100%;
  height: 100%;
  min-height: 600px;
  /* border: 2px solid tomato; */
}
 
.app span:nth-child(1) {
  position: absolute;
  background-color: rgb(188, 231, 225);
  width: 100%;
  height: 1.5px;
  box-shadow: 0 0 10px .5px rgba(255, 255, 255, 0.6);
  top: 0;
  left: 10%;
  transform: translate(-10%, -50%);
  transition: 1s cubic-bezier(0.785, 0.135, 0.15, 0.86);
  /* max-width: 100%; */
  max-width: 0%;
  /* animation: 1s rotate infinite both; */
}

.app span:nth-child(2) {
  position: absolute;
  background-color: rgb(188, 231, 225);
  width: 100%;
  height: 1.5px;
  box-shadow: 0 0 10px .5px rgba(255, 255, 255, 0.5);
  bottom: 0;
  right: -10%;
  transform: translate(-10%, -50%);
  transition: 1s cubic-bezier(0.785, 0.135, 0.15, 0.86);
  /* max-width: 100%; */
  max-width: 0%;
  /* animation: 1s rotate infinite both; */
}


.app span:nth-child(3) {
  position: absolute;
  background-color: rgb(188, 231, 225);
  width: 1.5px;
  height: 100%;
  box-shadow: 0 0 10px .5px rgba(255, 255, 255, 0.5);
  bottom: -30%;
  left: 0%;
  transform: translate(-10%, -30%);
  transition: 1s cubic-bezier(0.785, 0.135, 0.15, 0.86);
  /* max-height: 100%; */
  max-height: 0%;
  /* animation: 1s rotate infinite both; */
}

.app span:nth-child(4) {
  position: absolute;
  background-color: rgb(188, 231, 225);
  width: 1.5px;
  height: 100%;
  box-shadow: 0 0 10px .5px rgba(255, 255, 255, 0.5);
  top: 10%;
  right: 0%;
  transform: translate(-20%, -10%);
  transition: 1s cubic-bezier(0.785, 0.135, 0.15, 0.86);
  /* max-height: 100%; */
  max-height: 0%;
  /* animation: 1s rotate infinite both; */
}

.enterBoxNeon {
  max-height: 100% !important;
  max-width: 100% !important;
}

.leaveBoxNeon1 {
  left: 200% !important;
}

.leaveBoxNeon2 {
  right: 200% !important;
}

.leaveBoxNeon3 {
  bottom: 100% !important;
}

.leaveBoxNeon4 {
  top: 200% !important;
}

@keyframes rotate {
  0% {
    max-width: 0%;
  }
  50% {
    max-width: 100%;
  }
}


.navbarAndMainDiv {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: column;
  /* border: 1px solid red; */
}