.mainSliderDiv { 
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    /* border: 2px solid black; */
    margin-top: 20px;
}

.mainImageDiv {
    box-shadow: 0 10px 10px 0 rgba(0, 0, 0, 0.4);
    width: 500px;
    height: 250px;
    background: linear-gradient(white -50%, rgb(177, 177, 177) 50%, white 130%);
    border: 1px solid gray;
    padding: 10px;
    transition: 0.5s ease-in-out;
    transform: scale(0,0);
    opacity: 0;
    margin-left: 20px;
}

.mainImageDivShow {
    transform: unset;
    opacity: 1;
}


.mainImage {
    background-color: black;
    width: 100%;
    height: 100%;
}


.navigationImagesDiv {
    margin-top: 20px;
    /* border: 2px solid darkgreen; */
    display: flex;
    width: fit-content;
}

.imageNavItemWrapper {
    display: flex;
    flex-direction: column;
    height: 200px;
    /* border: 1px solid red; */
}

.imageNavItem {
    /* border: 1.5px solid black; */
    cursor: pointer;
    background-color: rgb(7, 23, 23);
    border: 1.5px solid transparent;
    width: 100px;
    height: 135px;
    box-shadow: 0 0 3px 0 black;
    transition: 0.5s;
    opacity: 0;
    transform: translateY(-20px);
}

.imageNavItemWrapper:hover .imageNavItem {
    border: 1.5px solid turquoise;
}

.imageNavItemWrapper:hover .imageNavItemReflection {
    transition: 0.3s;
    box-shadow: inset 0 2px 1px 0 rgba(64, 224, 208, 0.51);
}

.imageNavItemShow {
    opacity: 1;
    transform: translateY(0px);
}

.imageNavItemWrapper:not(:first-child) {
    margin-left: 50px;
}

.imageNavItemReflection {
    margin-top: 3px;
    height: 50px;
    width: 100%;
    background: linear-gradient(rgba(47, 79, 79, 0.5), rgba(0, 0, 0, 0));
    transition: 0.5s ease-in-out;
    opacity: 0;
    transform: translateY(20px);
}

.imageNavItemReflectionShow {
    opacity: 1;
    transform: translateY(0px);
}

/* ABeeZee|Allura|Italiana|Montserrat|Philosopher|Playball|Poiret+One|Quicksand|Sofia */
.imageNavArrow {
    cursor: pointer;
    font-size: 50px;
    font-family: Allura;
    transition: 0.5s ease-in-out;
    opacity: 0;
}

.imageNavArrow:hover {
    color: rgb(3, 66, 60);
    text-shadow:  0 0 5px turquoise;
}

.imageNavLeft {
    transform: translateX(-20px);
}

.imageNavRight {
    margin-left: 20px;
    transform: translateX(20px);
}

.imageNavArrowShow {
    opacity: 1;
    transform: unset;
}